img {
    width: 100px;
    height: 60px;
}
p {
    float: right;
}
p.active {
    color: green;
}
.loginBlock {
    outline: 1px solid blueviolet;
    padding: 3px 0;
    width: 88px;
    text-align: center;
    margin-left: 8px;
    background-color:aliceblue;
    cursor: pointer;
    border-radius: 0 10px 10px 0;
}
.loginBlock a {
    color: #333;
    font-weight: bold;
    letter-spacing: 0.2em;
}