html * {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body {
    display: flex;
    min-height: 100vh;
    width: 600px;
}
main {
    display: flex;
    gap: 50px;
}
nav ul {
    display: flex;
    flex-direction: column;
    gap: 30px;
    margin-left: 20px;
}
.navigation {
    list-style-type: none;
}
.navigation li:nth-last-child(1) {
    margin-top: 30px;
}